.wrapper{position:relative}.errorMessage{color:#d72e2f;font-size:12px;line-height:16px;margin-bottom:4px;display:block}.input{opacity:0;position:absolute;width:22px;height:22px;margin:0;bottom:9px;left:0;appearance:none}.label{display:flex;flex-direction:row;align-items:center}.label:before{content:"";width:22px;height:22px;line-height:22px;box-sizing:border-box;display:block;margin-right:16px;border:1px solid #1b9b9e;border-radius:3px;font-weight:900;color:#fff;text-align:center;font-size:14px;background-color:#e9f5f5;transition:background-color ease .15s,border-color ease .15s;cursor:pointer}.label:hover:before{background-color:#bce2e3}.label.invalid:before{border-color:#d72e2f;background-color:rgba(215,46,47,.1)}.label.invalid:hover:before{background-color:rgba(215,46,47,.3)}.input:checked+.label:before{content:"";background-color:#1b9b9e}.input:focus+.label:before{box-shadow:0 0 0 1px #1b9b9e}.input:checked+.label:hover:before{background-color:#1b9b9e;border-color:#1b9b9e}.label a{color:#1b9b9e;font-weight:700;text-decoration:none}.label a:hover{text-decoration:underline}